@import url("https://fonts.googleapis.com/css?family=Source+Sans+Pro:300,400,600,700,900");
@import "./variables.scss";
@import "./typography.scss";
@import "./mixins.scss";

@font-face {
  font-family: 'Apex Sans Regular';
  src: url('../fonts/ApexSansBook-Regular.woff2') format('woff2'),
      url('../fonts/ApexSansBook-Regular.woff') format('woff');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: 'Apex Sans Book';
  src: url('../fonts/ApexSansBook.woff2') format('woff2'),
      url('../fonts/ApexSansBook.woff') format('woff');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: 'Apex Sans Bold';
  src: url('../fonts/ApexSans-Bold.woff2') format('woff2'),
      url('../fonts/ApexSans-Bold.woff2') format('woff');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

html {
  -moz-text-size-adjust: none;
  -webkit-text-size-adjust: none;
  text-size-adjust: none;
}

body {
  background-color: $red;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a {
  color: inherit;
}

// button and link focus states
*:focus {
  outline: 3px solid black;
}

// Don't show focus outlines while things are being clicked
*:active {
  outline: none;
}

// Give the outline box some padding on buttons
button:focus {
  outline-offset: 10px;
}
.buttons > .btn:focus {
  outline-offset: 10px;
}

// Prevent Safari from adjusting font size when it thinks the font is too small
@media screen and (max-device-width: 480px){
  body{
    -webkit-text-size-adjust: none;
  }
}